.checkbox {
  appearance: none;
  position: relative;
  top: -2px;
  right: 0;
  bottom: 0;
  left: 0;
  height: 20px;
  width: 20px;
  transition: all linear 0.5s;
  background: $main-color;
  border: 4px solid $gray-lighten;
  color: $second-color;
  cursor: pointer;
  display: inline-block;
  margin-right: 0.5rem;
  outline: none;
  z-index: 10;

  &:hover {
    border-color: $second-color;
  }

  &:checked {
    background: $gray-lighten;
  }

  &:checked::before {
    content: '✔';
    position: absolute;
    top: -3px;
    left: 2px;
    display: inline-block;
    font-size: 12px;
  }

  &-wrap {
    position: absolute;
  }

  &__text {
    display: inline-block;
    max-width: 420px;
    font-size: 14px;
    line-height: 1.5;
    padding-left: 35px;
    margin-bottom: 35px;
  }

  &__link {
    color: $link-color;

    &:hover {
      color: $link-color-hover;
    }
  }
}

.checkbox--error {
  border-color: $error-color;

  &:checked {
    border-color: $gray-lighten;
  }
}
